/** @format */

.setting_company_nk {
	max-width: 100%;
	width: 100%;
	margin: auto;
	line-height: 27px;
	background: #eeeeee;
	min-width: 0;
	font-family: 'Open Sans', sans-serif;

	header {
		@include grid(16px, 3fr 1fr);
		margin-top: var(--block30);
		.header {
			display: grid;
			grid-gap: var(--block40);
		}
		.consultant {
			max-width: 100%;
			width: 100%;
			height: auto;
		}

		.block {
			border: 1px solid #f2f4f7;
			box-shadow: 0px 4px 4px rgba(68, 118, 145, 0.16);
			border-radius: 5px;
			@include grid(0px, auto 1fr);
			align-items: center;
			background: #fff;
			align-self: baseline;
			.block-tariff {
				background: $background-lightBlue;
				padding: var(--block40);
				width: fit-content;
				.section-act {
					margin-top: var(--block40);
					p {
						color: $colorOrange;
						font-weight: 600;
					}
				}
			}
			.block-info {
				@include grid(80px, 0.2fr 0.4fr);
				justify-content: center;
				padding: 0 20px;

				.item {
					transition-duration: 0.2s;
					@include grid(16px, auto 1fr);
					align-items: center;
					p {
						max-width: 165px;
						min-width: 165px;
					}
				}
				.item:hover {
					color: $corporate_color !important;
					cursor: pointer;
				}
				.item:hover > img {
					position: relative;
					animation: order-page_header-icon 0.8s ease;
				}
				@keyframes order-page_header-icon {
					0% {
						left: 0px;
					}
					50% {
						left: 10px;
					}
					100% {
						left: 0px;
					}
				}
			}

			.block-question {
				background: $background-Blue;
				padding: 50px 60px;
			}
			.block-note {
				padding: 0px 60px;
				color: $corporate_color;
			}
		}
	}

	.reviews-orders {
		.order {
			margin-top: var(--block40);
		}
		.reviews {
			margin-top: var(--block40);
		}
		.blocks {
			margin-top: var(--block30);
			@include grid(var(--block40), 1fr 1fr);
			.block {
				background: #ffffff;
				border: 1px solid #f2f4f7;
				box-shadow: 0px 4px 4px rgba(68, 118, 145, 0.16);
				border-radius: 5px;
				@include grid(0px, 2fr auto);
				position: relative;
				.text {
					padding: 30px 30px 40px 30px;
				}
				.text-img {
					max-width: 76%;
					z-index: 2;
				}
				.header {
					@include grid(16px, auto 1fr);
					color: $corporate_color;
					align-items: start;
					margin-bottom: 10px;
				}
				.note {
					margin-left: 55px;
				}
				.img {
					display: grid;
					justify-content: end;
					width: fit-content;
					position: absolute;
					height: 100%;
					right: 0;
					justify-items: right;
					.mobile {
						display: none;
					}
				}
			}
			.img {
				img {
					height: 100%;
					max-width: 100%;
					width: 100%;
				}
			}
		}
		.blocks_multi {
			@include grid(var(--block40), 1fr 1fr 1fr);
			.text {
				display: grid;
				grid-template-rows: 1fr 1fr;
			}
			.heading {
				max-width: 238px;
			}
		}
	}
	.efficiency {
		margin-top: var(--block40);
		.blocks {
			@include grid(var(--block40), 1fr 1fr);
			margin-top: var(--block40);
			.block {
				overflow: hidden;
				background: #ffffff;
				border: 1px solid #f2f4f7;
				box-shadow: 0px 4px 4px rgba(68, 118, 145, 0.16);
				border-radius: 5px;
				position: relative;
				display: grid;
				grid-template-columns: auto auto;
				.textarea {
					display: grid;
					grid-gap: var(--block30);
					padding: 30px 0px 40px 30px;
					max-width: 70%;
					z-index: 1;
				}
				.heading {
					color: $corporate_color;
					font-weight: 700;
				}
				.img {
					position: absolute;
					right: 0;
					display: grid;
					justify-items: end;
				}
			}
			.block-disable {
				opacity: 0.2;
			}
		}
	}
	.sale {
		margin-top: var(--block40);
		.subtitle {
			margin-top: 20px;
			color: #5e7491;
		}
		.block {
			background: #ffffff;
			border: 1.5px solid #f2f4f7;
			box-shadow: 0px 4px 4px rgba(68, 118, 145, 0.16);
			border-radius: 5px;
			padding: 40px;
			overflow: hidden;
			color: #8f9eb2;
			margin-top: 30px;
			min-width: 0;
			display: grid;
			align-content: center;
			position: relative;
		}
		.block__grid {
			@include grid(16px, 1fr 2.5fr);
			min-width: 0;
			max-width: 100%;
			.block-info {
				position: relative;
				z-index: 1;
				.illustration {
					position: absolute;
					top: -144px;
					left: 247px;
				}
				.block__heading {
					margin-top: 20px;
					color: $text;
				}
				.block__subtitle {
					margin-top: 10px;
					color: #8f9eb2;
					max-width: 350px;
				}
				.line {
					width: 100px;
					height: 2px;
					background: $corporate_color;
					margin: 30px 0;
				}
			}
		}
		.block__grid_russia {
			@include grid(16px, 1fr 1fr);
			.block-info {
				.block__subtitle {
					max-width: 100%;
					color: $text;
				}
				.line {
					width: 100%;
					max-width: 400px;
				}
			}
			.list__grid {
				grid-template-columns: 1fr 1fr;
				margin-top: 10px;
				grid-gap: 12px;
			}
		}
		.block_goods {
			.block__grid {
				grid-template-columns: 1fr 1fr;
				.block-info {
					.block__subtitle {
						max-width: 100%;
						color: $text;
					}
				}
			}
			.block__img {
				img {
					max-width: 120%;
					position: absolute;
					bottom: -30px;
					right: 0;
				}
			}
		}

		.tarifs {
			@include grid(30px, 1fr 1fr 1fr);
			z-index: 1;
			img {
				max-width: 100%;
			}
			.tarif {
				background: #ffffff;
				box-shadow: 0px 4px 4px rgba(68, 118, 145, 0.16);
				border-radius: 5px;
				padding: 30px;
				display: grid;
				grid-template-rows: auto 1fr auto;
				align-items: baseline;
				.tarif__header {
					background: rgba(2, 141, 216, 0.1);
					border-radius: 30px;
					padding: 11px 36px;
					color: $corporate_color;
					@include grid(10px, 1fr 1fr);
					align-items: center;
					color: $corporate_color !important;
				}
			}
			.heading_Button {
				width: 100%;
			}
		}
		.list__grid {
			grid-gap: 20px;
			display: grid;
		}
		.block__img {
			display: grid;
			justify-content: flex-end;
			position: relative;
			left: 40px;
			img {
				max-width: 100%;
			}
		}
		.list__note {
			color: #8f9eb2;
			opacity: 0.5;
		}
	}
	.article {
		margin-top: var(--block30);
		.slider__slide {
			background: #ffffff;
			border: 1.3546px solid #f2f4f7;
			box-sizing: border-box;
			box-shadow: 0px 4px 4px rgba(68, 118, 145, 0.16);
			border-radius: 5.4184px;
			display: grid;
			grid-template-rows: 1.5fr auto;
			cursor: pointer;
			margin-top: var(--block30);
			.slider__text {
				align-content: baseline;
				display: grid;
				// grid-auto-rows: 1fr 1.5fr;
				grid-gap: 20px;
				padding: 30px;
				padding-bottom: 0;
				margin-bottom: 30px;
				grid-template-rows: 1fr 1.5fr;
			}
			img {
				max-width: 100%;
				width: 100%;
			}
		}
	}

	.slider {
		position: relative;
	}
	.swiper-slide {
		display: grid;
		height: auto;
	}
	.article-button-prev,
	.article-button-next {
		display: grid;
		justify-content: center;
		align-content: center;
		background: #028dd8;
		box-shadow: 0px 4px 10px rgb(0 88 125 / 36%);
		border-radius: 54px;
		width: 50px;
		height: 50px;
		margin-right: 16px;
		position: absolute;
		z-index: 1;
		top: 50%;
		transform: translateY(-50%);
	}
	.article-button-prev:hover,
	.article-button-next:hover {
		cursor: pointer;
		box-shadow: 0px 10px 20px rgb(2 141 216 / 25%);
	}
	.article-button-disabled {
		display: none;
	}
	.article-button-prev {
		left: -20px;
	}
	.article-button-next {
		right: -34px;
	}
	.switch-js {
		display: none;
	}
	.switch-js-block {
		display: block;
	}
	.consultant-mobile {
		display: none;
		margin-top: var(--block30);
	}

	@media (max-width: 1840px) {
		.main-content {
			font-size: 16px;
			min-width: 0;
		}
	}

	@media (max-width: 1540px) {
		.order-page {
			.reviews-orders {
				.blocks {
					.block {
						.text-img {
							max-width: 70%;
						}
						.img {
							.mobile {
								display: block;
							}
							.desktop {
								display: none;
							}
						}
					}
				}
			}
		}
	}

	@media (max-width: 1440px) {
		.order-page {
			.list {
				font-size: 14px;
			}
			header {
				grid-template-columns: 1fr;
				.block {
					.block-info {
						.item p {
							max-width: 164px;
							min-width: 164px;
						}
					}
				}
				.consultant {
					display: none;
				}
			}
			.reviews-orders {
				.blocks_multi {
					.text {
						grid-template-rows: 1fr 1.5fr;
					}
				}
			}
			.consultant-mobile {
				display: block;
				height: 558px;
				width: 100%;
			}
			.sale {
				.tarifs {
					grid-gap: 16px;
					.tarif {
						padding: 16px;
						.tarif__header {
							padding: 11px 16px;
							font-size: 14px;
						}
						.tarif__header:hover {
							cursor: unset;
						}
					}
				}
			}
			// .article {
			// 	.slider__slide {
			// 		grid-template-rows: 2fr 1fr;
			// 	}
			// }
		}
	}
	@media (max-width: 1366px) {
		.main-content {
			font-size: 14px;
		}
		.order-page {
			.sale {
				.block__grid {
					grid-template-columns: 1fr;
					.block-info {
						@include grid(16px, auto 1fr);
						.illustration {
							max-width: 179px;
							position: absolute;
							top: -122px;
							right: -24px;
							left: inherit;
						}

						.list__grid {
							@include grid(16px, 1fr 1fr);
						}
						.line {
							display: none;
						}
						.list__heading {
							max-width: 266px;
						}
					}
				}

				.block_russia {
					align-content: baseline;
					.block__grid_russia {
						.block-info {
							grid-template-columns: 1fr;
							.line {
								display: block;
							}
							.block__heading {
								margin-top: 0;
							}
						}
						.list {
							margin: 0px 0;
							grid-gap: 10px;
						}
						.block__img {
							position: inherit;
							img {
								position: absolute;
								top: 0px;
								left: auto;
								right: 0;
								opacity: 0.2;
								max-width: 44%;
							}
						}
					}
					.block__img_region {
						position: relative;
						right: 0;
						img {
							position: relative;
							top: 0;
							left: 0;
						}
					}
					.block_russia-header {
						@include grid(0px, 1fr);
						max-width: 600px;
					}
				}
				.block_russia-slide {
					.block__grid_russia {
						.block__img img {
							position: relative;
							opacity: 1;
							left: 30px;
							width: auto;
							display: grid;
							margin-left: auto;
						}
					}
				}
				.block_goods {
					.block__grid {
						.block-info {
							grid-template-columns: 1fr;
							.list__grid {
								grid-template-columns: 1fr;
							}
						}
					}
					// .block__img {
					// 	img {
					// 		max-width: 120%;
					// 		position: absolute;
					// 		bottom: -30px;
					// 		right: 0;
					// 	}
					// }
				}
				.block {
					padding: 30px;
				}

				.tarifs {
					.tarif {
						padding: 30px;
						.tarif__header {
							padding: 11px 30px;
						}
					}
				}
			}
			// .article {
			// 	.slider__slide {
			// 		grid-template-rows: 1.1fr 1fr;
			// 	}
			// }
		}
		:root {
			--block40: 30px;
		}
	}

	@media (max-width: 1240px) {
		:root {
			--block40: 20px;
		}
		.order-page {
			.reviews-orders {
				.blocks_multi {
					display: grid;
					grid-template-columns: 1fr 1fr;
					grid-gap: var(--block40);
				}
			}
			header {
				.block {
					grid-template-columns: 1fr 1fr;
					.block-info {
						grid-template-columns: 1fr;
						padding: var(--block30) 20px;
						grid-gap: 20px;

						.item p {
							max-width: 250px;
						}
					}
					.block-tariff {
						width: 100%;
						height: 100%;
					}
				}
				.blockQuestion {
					@include grid(0px, auto 1fr);
				}
			}
			.reviews-orders {
				.blocks {
					.block {
						.text-img {
							max-width: 100%;
						}
						.img {
							.mobile {
								display: none;
							}
						}
					}
				}
			}
			.efficiency {
				.blocks {
					.block {
						.textarea {
							grid-gap: 20px;
							max-width: 93%;
						}
						.efficiency__btn {
							margin-top: 150px;
						}
						.img {
							bottom: -20px;
						}
					}
				}
			}
			.sale {
				.block_goods {
					.block__grid {
						grid-template-columns: 1fr;
						.block__img img {
							position: relative;
						}
					}
				}
			}
		}
	}
	@media (max-width: 993px) {
		.order-page {
			max-width: 100%;
			padding: 20px;
			background: #f6f6f6;
			.reviews-orders {
				.blocks {
					.block {
						.text {
							grid-template-rows: 1fr 1fr;
							padding: 20px;
						}
					}
				}
			}
			.sale {
				.block__grid {
					.block-info {
						grid-template-columns: 1fr;
					}
				}
			}
			.article {
				.article-button-prev,
				.article-button-next {
					width: 40px;
					height: 40px;
				}
			}
		}
	}
	@media (max-width: 768px) {
		.order-page {
			.sale {
				.block-info {
					grid-template-columns: 1fr;
					.list {
						margin-top: 0px;
					}

					.illustration {
						top: -30px;
						right: 0;
					}
				}
				.tarifs {
					.tarif {
						padding: 16px;
						.tarif__header {
							grid-template-columns: 1fr;
							justify-items: center;
							grid-gap: 0;
							padding: 11px 16px;
							img {
								max-width: 100px;
							}
						}
						.list {
							font-size: 12px;
							grid-gap: 8px;
						}
					}
				}
				.block_russia-slide {
					.block__grid_russia {
						.block__img img {
							position: absolute;
							opacity: 0.2;
							max-width: 44%;
							right: 0;
						}
					}
				}
			}
		}
	}

	@media (max-width: 660px) {
		.section-h1 {
			font-size: 20px;
			font-weight: 600;
			line-height: initial;
		}
		.order-page {
			.section-h2 {
				font-size: 18px;
			}
			header {
				.block {
					grid-template-columns: 1fr;
					.block-info {
						display: none;
					}
					.block-question {
						padding: 8px 8px;
						display: grid;
						justify-content: center;
						img {
							max-width: 8px;
						}
					}
					.block-note {
						padding: 20px 20px;
					}
				}
			}
			.reviews-orders {
				.blocks {
					.block {
						.header {
							grid-gap: 9px;
						}
						.text {
							padding: 10px;
							grid-template-rows: 1fr;
						}
						.note {
							display: none;
						}
						.header {
							grid-template-columns: 1fr;
						}
					}
				}
			}
			.efficiency {
				.blocks {
					grid-template-columns: 1fr;
					grid-gap: var(--block40);
					margin-top: var(--block40);
					.block {
						.textarea {
							padding: 20px 0px 20px 20px;
						}
						.heading {
							max-width: 180px;
						}
					}
				}
			}
			.sale {
				.block__grid {
					.block-info {
						.illustration {
							top: -116px;
							right: -56px;
						}
						.list__grid {
							grid-template-columns: 1fr;
						}
						.list__heading {
							max-width: 100%;
						}
					}
				}
				.block_russia {
					.block_russia-header {
						grid-template-columns: 1fr;
					}
					.block__img img {
						position: absolute;
						top: -97vh;
						left: 37%;
						opacity: 0.2;
						max-width: 61%;
					}
					.block__img_region {
						position: inherit;
						right: 0;
						img {
							position: absolute;
							top: -3vh;
							left: auto;
							right: -25%;
							opacity: 0.8;
						}
					}
				}
				.block_goods {
					.block__grid {
						.block__img {
							position: inherit;
							img {
								position: absolute;
								top: 0;
								right: 0;
							}
						}
					}
				}

				.tarifs {
					grid-template-columns: 1fr;
					.tarif {
						padding: 0;
						box-shadow: none;
						.tarif__header {
							grid-template-columns: 1fr 1fr;
							justify-items: center;
							grid-gap: 0;
							border: 2px solid $corporate_color;
							background: none;
							transition-duration: 0.5s;
							img {
								max-width: 100px;
							}
						}
						.tarif__header:hover {
							cursor: pointer;
							background: $corporate_color;
							color: #fff !important;
						}
						.list {
							display: none;
						}
						.heading_Button {
							display: none;
						}
					}
				}
			}
		}
	}

	@media (max-width: 400px) {
		.order-page {
			.toolbar-rascal {
				display: none;
			}
		}
	}
}
